import { gql } from '@apollo/client';

export const POLICIESMODULES = gql`
  query policyModules($filter: PolicyModuleFilterInput) {
    policyModules(filter: $filter) {
      pageInfo {
        hasNextPage
        hasPreviousPage
        __typename
      }
      totalCount
      items {
        id
        policyType
        address
        phoneNumber
        startDateVigency
        endDatevigency
        moneySymbols {
          id
          value
          label
          keyName
          __typename
        }
        dateLimitVigency
        description
        policyDeductibleId
        deductibles {
          deductibleTheft
          __typename
        }
        policyFrequencyPayment {
          monthly
          annual
          __typename
        }
        modules {
          key
          typeValue
          name
          id
          createdAt
          updatedAt
          __typename
        }
        products {
          name
          __typename
        }
        declarations {
          email
          __typename
        }
        createdAt
        updatedAt
        __typename
      }
      __typename
    }
  }
`;
